<script>
  import { onMount } from "svelte";
  import { getRequest } from "./js/Viewer";
  import ImageSwipe from "../components/ImageSwipe/ImageSwipe.svelte";
  import { pop } from "svelte-spa-router";

  export let params = {};
  var requestNo = params.request_no;
  var now = params.now;
  var images = [];

  onMount(async () => {
    await getRequest(requestNo).then(async (res) => {
      images = res.data.image_url.split(",");
      if (!now) now = 0;
    });
    console.log(images);
    // swiperRef.slideTo(now, 10, false);
  });
</script>

<main>
  <button
    class="btn btn-dark m-3"
    style="position:fixed;z-index: 1;"
    on:click={() => pop()}>돌아가기</button
  >
  {#if images[0]}
    <ImageSwipe bind:images bind:index={now} />
  {/if}
</main>

<style>
  main {
    height: 100%;
    width: 100%;
    margin: 0;
    background: #000 no-repeat center center fixed;
  }
</style>
