<script>
  import { onMount } from "svelte";

  import { Swipe, SwipeItem } from "svelte-swipe";
  export let images = [];
  export let index = 0;
  onMount(() => {
    changeSlide(index);
  });

  let swiper;
  function changeSlide(i) {
    swiper.goTo(i);
  }

  const swipeConfig = {
    autoplay: false,
    delay: 2000,
    showIndicators: true,
    transitionDuration: 1000,
    defaultIndex: 0,
  };
</script>

<Swipe {swipeConfig} bind:this={swiper}>
  {#each images as image, i}
    <SwipeItem>
      <div class="d-flex justify-content-center align-items-center p-5 h-100">
        <img src={image} alt="" class="m-auto h-auto mw-100 mh-100" />
      </div>
    </SwipeItem>
  {/each}
</Swipe>

<style>
  :global(.swipeable-item-inner) {
    height: 100%;
    max-height: 100%;
  }
</style>
